'use client'

import { z } from "zod"
import { Button } from "../ui/Button/Button"
import { FormInput } from "../ui/FormInput/FormInput"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod";
import { user } from "@/api/user"
import { useState } from "react"

// const authFormSchema = z.object({
//         email: z.string().email(),
//         password: z.string(),
//     })

// type AuthFormType = z.infer<typeof authFormSchema>

export const AuthForm = () => {

    // const {register, handleSubmit, formState: { errors }} = useForm<AuthFormType>({
    //     resolver: zodResolver(authFormSchema)
    // })

    // const handleClickAuth = (data: AuthFormType) => {
    //     user(data.email, data.password)
    // }

    const [email, setEmail] = useState<string>('')
    const [password, setPassword] = useState<string>('')

    const handleSubmit = (data: {email: string, password: string}) => {
        user(data.email, data.password)
    }

    return (
        <>
            <form name="authForm" className="flex flex-col gap-[20px] items-center justify-center border-1 border-sky-300 pt-20 pb-20 pl-10 pr-10 rounded-[20px] shadow-md">
                <h2 className="font-medium text-lg m-0">Авторизация</h2>
                <FormInput placeholder="E-mail" type="text" label="E-mail" value={email} onChange={(e) => setEmail(e.target.value)}/>
                <FormInput placeholder="Пароль" type="password" label="Пароль" value={password} onChange={(e) => setPassword(e.target.value)}/>
                <Button classname={"font-medium bg-blue-500 w-40 h-10 rounded flex justify-center items-center text-white cursor-pointer"} type="submit">
                    Отправить
                </Button>
                <Button classname="cursor-pointer" onClick={() => handleSubmit}>
                    Регистрация
                </Button>
            </form>
        </>
    )
}